<template>
  <div id="app">
    <!-- {{ $t("message.hello") }} -->
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
    <h1 class="text-white">
      Looking for a software developer?
    </h1>
    <h2 id="fancy" ref="fancy"  @click="behold" class="font-bold italic text-white transition-color duration-500 ease-in">
      {{ $t("intro") }}
    </h2>
    <Portfolio msg="Welcome to Your Vue.js App" />
  </div>
</template>

<script>
import Portfolio from "./components/Portfolio.vue";

export default {
  name: "App",
  components: {
    Portfolio,
  },
  mounted: function () {
    this.behold();
  },
  data: () => {
    return {
      isGettingFancy: true,
      timer: null,
    };
  },
  methods: {
    behold() {
      if (this.isGettingFancy) {
        this.timer = setInterval(() => {
          const randomColor = Math.floor(
            Math.random() * (16777215 - 1052688) + 1052688
          ).toString(16);
          this.$refs.fancy.style.color = `#${randomColor}`;
        }, 750);
      } else {
        clearInterval(this.timer);
      }
      this.isGettingFancy = !this.isGettingFancy;
    },
  },
};
</script>

